<template>
  <div id="wrapper" class="bg-white">
    <!-- Header -->
    <header id="header">
      <div class="input-group">
        <div class="input-group-btn">
          <button type="button" class="ui-nav nav-back" @click="history.back();"></button>
        </div>
        <h1></h1>
      </div>
    </header>
    <!-- //Header -->
    <!-- Content -->
    <section id="content">
      <div class="visual-banner middle-banner">
        <p class="msg">finset  </p>
        <p class="msg">   </p>
        <p class="msg"></p>
        <br/>
        <p class="msg">   </p>
        <p class="msg"> </p>
        <br/>
        <p class="msg">  </p>
        <p class="msg">      .</p>
      </div>
      <div class="container">
        <table class="table tbl_status">
          <thead>
            <tr>
              <th colspan="3">  </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="offerItem in offerList" :key="offerItem.index"
              @click="offerItemClick(offerItem.seq_share, offerItem.share_status, offerItem.yn_offer
                              , offerItem.yn_itgt_mngm, offerItem.dt_end_offer);">
              <td>{{offerItem.offer_nm_person}}</td>
              <td v-if="offerItem.share_status === '01'">
                
              </td>
              <td v-else-if="offerItem.share_status === '02'">
                
              </td>
              <td>{{offerItem.offer_hp}}</td>
            </tr>
          </tbody>
        </table>
        <table class="table tbl_status">
          <thead>
            <tr>
              <th colspan="3">  </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="reqItem in reqList" :key="reqItem.index"
            @click="reqItemClick(reqItem.seq_share, reqItem.share_status, reqItem.yn_offer
                      , reqItem.yn_itgt_mngm, reqItem.dt_end_offer);">
              <td>{{reqItem.req_nm_person}}</td>
              <td v-if="reqItem.share_status === '01'">
                
              </td>
              <td v-else-if="reqItem.share_status === '02'">
                
              </td>
              <td>{{reqItem.req_hp}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="btn-fixed-bottom affix-bottom">
        <a role="button" class="btn btn-lg btn-block btn-primary" @click="createNewRequest();"> </a>
      </div>
    </section>
    <!-- //Content -->
  </div>
</template>

<script>

// import Common from './../../assets/js/common.js'
import router from '@/comm/router.js'

export default {
  name: 'shareSumMain',
  data() {
    return {
      errors: [],
      errMsg: '',
      offerList: [],
      reqList: [],
      currentDate: '',
      seq_share: ''
    }
  },
  component: {
  },
  // computed () {
  // },
  beforeCreate() {
  },
  created() {
  },
  beforeMount() {
  },
  mounted() {
  },
  beforeUpdate() {
  },
  updated() {
  },
  beforeDestroy() {
  },
  destroyed() {
  },
  methods: {
    getSumMainInfo(){
       var _this = this;
      this.$http.get('/m/customercenter/shareInfoSummary.json', {
        params: {}
      }).then(function (response) {
        _this.offerList = response.data.offerList
        _this.reqList = response.data.reqList
        _this.currentDate = response.data.currentDate
      })
    },
    offerItemClick(seq_share, share_status, yn_offer, yn_itgt_mngm, dt_end_offer){
      var _this = this;
      _this.seq_share = seq_share

      if(share_status === "01"){ // 
        this.goSetting('01')
      }else if(share_status === "02"){ //
        if(yn_offer === 'Y' && yn_itgt_mngm === 'N'){  // 
          if(dt_end_offer >= _this.currentDate){ //     
            this.viewDetail();
          }
        }

        if(yn_itgt_mngm === 'Y'){ // 
          this.viewDetail();
        }
      }
    },
    reqItemClick(seq_share, share_status, yn_offer, yn_itgt_mngm, dt_end_offer){
      var _this = this;
      _this.seq_share = seq_share
      if(share_status === '01'){ ///  
       this.goSetting('02');
      }else if(share_status === '02'){ ///  
        if(yn_offer === 'Y' && yn_itgt_mngm === 'N'){  // 
          if(dt_end_offer >= _this.currentDate){ //     
            this.goSetting('03');
          }
        }

        if(yn_itgt_mngm === 'Y'){ // 
          this.goSetting('03');
        }
      }
    },
    goSetting(setting_mode){ // 
      router.push({
                  name:"shareSeeting",
                  params: {
                          setting_mode: setting_mode,
                          seq_share: _this.seq_share
                          }
                  });
    },
    viewDetail(){ // 
       router.push({
                  name:"shareDetail",
                  params: {
                          seq_share: _this.seq_share
                          }
                  });
    },
    createNewRequest(){
      location.href = "/share/newRequest"
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">

</style>



